* {
  box-sizing: border-box;
  margin: 0;
}
h1 {
  text-align: center;
  border-bottom: 1px solid black;
}
.option {
  border: 0.5px solid grey;
  width: 200px;
  height: fit-content;
  padding: 5px;
  margin: 2px;
  border-radius: 10px;
}
.question {
  display: flex;
  flex-wrap: wrap;
}
